import React, { Component } from 'react';
import './App.css';
import 'bulma/css/bulma.css'
import petco from './img/petco.jpg'
import rumham from './img/rumham.png'
import rivers from './img/DontBlameMeRivers.jpg'

class App extends Component {

   constructor(props) {
    super(props);
    this.state = {
      currentPage: 'Home'
    };
  }
  handleHomeClick(){
     this.setState({
        currentPage: 'Home'
    })
  }
  handleFactsClick(){
     this.setState({
        currentPage: 'Facts'
    })
  }
  handleScheduleClick(){
    this.setState({
      currentPage: 'Schedule'
    })
  }

  render() {
    return (
      <div className="App">
          <section class="hero is-dark is-medium is-bold">
          <div class="hero-head">
            <nav class="navbar">
              <div class="container">
                <div class="navbar-brand">
                  <a class="navbar-item">
                    <h1 class="title"> THE OPEN </h1>
                  </a>
                </div>
                <div id="navbarMenuHeroA" class="navbar-menu">
                  <div class="navbar-end">
                    <a id='homeMenu' onClick={() => {this.handleHomeClick()}} className={(this.state.currentPage == 'Home' ? 'is-active' :'') + ' navbar-item'}>
                      Home
                    </a>
                    <a id='factsMenu' onClick={() => {this.handleFactsClick()}} className={(this.state.currentPage == 'Facts' ? 'is-active' :'') + ' navbar-item'}>
                      F.A.C.T.S
                    </a>
                    <a id='factsMenu' onClick={() => {this.handleScheduleClick()}} className={(this.state.currentPage == 'Schedule' ? 'is-active' :'') + ' navbar-item'}>
                      Schedule
                    </a>
                  </div>
                </div>
              </div>
            </nav>
          </div>

          <div class="hero-body">
            <div class="container has-text-centered">
              <HeroContainer currentPage={this.state.currentPage} />
            </div>
          </div>
          <div class="hero-foot">
          </div>
        </section>
        <BodyContainer currentPage={this.state.currentPage}/>
      </div>
    );
  }
}

function HeroContainer(props){
  if(props.currentPage === 'Home'){
      return <HomeHero/>
  }
  else if(props.currentPage === 'Facts'){
    return <FactsHero/>
  }
  else{
    return <ScheduleHero />
  }

}

function BodyContainer(props){
  if(props.currentPage === 'Home'){
      return <Home/>
  }
  else if(props.currentPage === 'Facts'){
    return <Facts/>
  }
  else{
    return <Schedule/>
  }
}

function HomeHero(props){
  return(
    <div>
      <h1 class="title">
        The David "Two Plates" Levitas Memorial Open
      </h1>
      <h2 class="subtitle">
        Broken Arms, Swallowed Dice, Blonde Mustaches: What could go wrong?
      </h2>
    </div>
  )
}

function FactsHero(props){

  return(
    <div>
      <h1 class="title">
        Flipability, Agility, Chugability, Tolerance, Stamina
      </h1>
      <h2 class="subtitle">
        If you're gonna win, you gotta have the F.A.C.T.S.
      </h2>
    </div>
  )
}

function ScheduleHero(props){

  return(
    <div>
      <h1 class="title">
        A Day of Triumph
      </h1>
      <h2 class="subtitle">
        Just kidding mainly beer and silly things
      </h2>
    </div>
  )
}
class Facts extends Component {
  render(){
    return(
      <section class="hero">
        <div class="hero-body">
          <div class="container">
            <h1 class="title mb-2 mt-2">
              F.A.C.T.S.
            </h1>
            <h1 class="title">
               <a href="https://goo.gl/forms/gGZO3CkGs4ZId7LD3"> Submit your Power Rankings Here </a>
            </h1>
          </div>
        </div>
      </section>
    )
  }
}

class Schedule extends Component {
  render(){
    return(
      <section class="hero">
        <div class="hero-body">
          <div class="container">
            <h1 class="title mt-5">
              Schedule of events will be announced soon
            </h1>
          </div>
        </div>
      </section>
    )
  }
}

class Home extends Component {
  render(){
    return(
       <div class="container"> 
         <section>
          <div class="columns has-text-centered level mt-2">
            <div class="column">
              <h1 className="title">What?</h1>
              <h2 className="subtitle">A test of wits, skill, and talent</h2>
            </div>
            <div class="column is-half">
             <img src={rivers}/>
            </div>
          </div>
        </section>
        <section class="section">
          <div class="columns has-text-centered level">
            <div class="column is-half">
              <img src={petco}/>
            </div>
            <div class="column">
              <h1 className="title">Where?</h1>
              <h2 className="subtitle">The Beaches of San Diego</h2>
            </div>
          </div>
          </section>
          <section>
          <div class="columns has-text-centered level">
            <div class="column">
              <h1 className="title">Who?</h1>
              <h2 className="subtitle">The best California has to offer</h2>
            </div>
            <div class="column is-half">
             <img src={rumham}/>
            </div>
          </div>
        </section>
       </div>
     )
   }
}

export default App;
